// ====================  Vue  (Vue Core Module Imports) ====================

/**
 * Vue 3 
 *  createApp  Vue  Composition API
 * - createApp: Vue 3  API
 * -  Vue 2  new Vue() TypeScript 
 */
import { createApp } from 'vue';

/**
 * Pinia 
 * Vue 3  TypeScript 
 * - createPinia:  Pinia 
 * -  Vuex API 
 */
import { createPinia } from 'pinia';

// ====================   (Application Component Imports) ====================

/**
 * 
 * 
 * - App.vue: 
 * -  './App.vue'  src  App.vue 
 */
import App from './App.vue';

/**
 * Vue Router 
 * 
 * - router: 
 * -  './router'  src/router/index.js 
 */
import router from './router';

// ====================   (Third-Party Style Files) ====================

/**
 * Bootstrap 5 CSS 
 * UI 
 * - 5.3.0
 * - 'bootstrap/dist/css/bootstrap.min.css'
 * - 
 * - min.css 
 */
import 'bootstrap/dist/css/bootstrap.min.css';

/**
 * Font Awesome 
 * 
 * - 6.7.2
 * - '@fortawesome/fontawesome-free/css/all.min.css'
 * - solidregularbrands
 * - 
 */
import '@fortawesome/fontawesome-free/css/all.min.css';

// ====================   (Custom Style Files) ====================

/**
 * 
 *  CSS 
 * 
 * - './assets/css/common.css'
 * -  CSS 
 * - 
 */
import './assets/css/common.css';

// ====================   JavaScript  (Third-Party JavaScript Files) ====================

/**
 * Bootstrap 5 JavaScript 
 *  Popper.js
 * -  (Dropdown): 
 * -  (Modal): 
 * -  (Tooltip): 
 * -  (Popover): 
 * -  (Collapse): 
 * -  (Carousel): 
 * - 5.3.0
 * - 'bootstrap/dist/js/bootstrap.bundle.min.js'
 * - bundle  Popper.js 
 */
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

// ====================   (Development Debug Information) ====================

/**
 * 
 * 
 * -  console.log 
 * - 
 * -  emoji 
 */

// ====================  Vue  (Vue Application Instance Creation) ====================

/**
 *  Vue 3 
 *  createApp  App
 * - createApp(App):  Vue App 
 * - 
 * - Vue 3  API Vue 2  new Vue() 
 *
 * @type {App} Vue 
 */
const app = createApp(App);

// ====================  Pinia  (Pinia State Management Instance Creation) ====================

/**
 *  Pinia 
 * Pinia  Vue 3  TypeScript 
 *  API 
 * - createPinia():  Pinia 
 * - 
 * - 
 * - 
 *
 * @type {Store} Pinia 
 */
const pinia = createPinia();

// ====================   (Router System Registration) ====================

/**
 *  Vue Router 
 * 
 * - 
 * - 
 * - 
 * -  URL 
 *
 * app.use(router):  Vue 
 */
app.use(router);

// ====================   (State Management System Registration) ====================

/**
 *  Pinia 
 * 
 * - 
 * -  UI
 * - 
 * - 
 *
 * app.use(pinia):  Pinia  Vue 
 */
app.use(pinia);

// ====================   (Application Mounting) ====================

/**
 *  Vue  DOM 
 *  index.html  id="app" 
 * 
 * - app.mount('#app'):  DOM 
 * - '#app': DOM  HTML  <div id="app">
 * - 
 * - mountedcreated 
 *
 * @param {string} '#app' - DOM 
 */
app.mount('#app');

