<template>
  <div class="ask-container">
    <div class="ask-card">
      <h1 class="title">  RAG </h1>
      <p class="description">
         <strong>rag_db.zip</strong> AI 
      </p>

      <!--  -->
      <div class="tip">
         <strong></strong> <strong>rag_db.zip</strong> PDF
      </div>

      <!--  -->
      <div
        class="upload-area"
        @click="$refs.fileInput.click()"
        @dragover.prevent="handleDragOver"
        @dragleave.prevent="handleDragLeave"
        @drop.prevent="handleDrop"
        :class="{ 'drag-over': isDragOver }"
      >
        <input
          ref="fileInput"
          type="file"
          accept=".zip"
          @change="handleFileSelect"
          style="display: none;"
        />
        <span>{{ uploadText }}</span>
      </div>

      <!--  -->
      <div v-if="selectedFile" class="file-info">
         : {{ selectedFile.name }}
      </div>

      <!--  -->
      <div class="question-area">
        <label class="question-label" for="question-input"></label>
        <textarea
          id="question-input"
          class="question-input"
          rows="4"
          placeholder=""
          v-model.trim="questionText"
          @input="clearResult"
        ></textarea>
      </div>

      <!--  -->
      <button
        class="submit-btn"
        @click="submitQuestion"
        :disabled="!selectedFile || !questionText || isProcessing"
      >
        <span v-if="isProcessing" class="loader"></span>
        <span>{{ buttonText }}</span>
      </button>

      <!--  -->
      <div v-if="statusMessage" class="status" :class="statusClass">
        {{ statusMessage }}
      </div>

      <!--  -->
      <div v-if="resultAnswer" class="result">
        <div class="result-section">
          <div class="result-title"></div>
          <div class="result-content">{{ resultQuestion }}</div>
        </div>
        <div class="result-section">
          <div class="result-title"></div>
          <div class="result-content">{{ resultAnswer }}</div>
        </div>
        <div v-if="resultSources.length" class="result-section">
          <div class="result-title"></div>
          <ul class="result-sources">
            <li v-for="source in resultSources" :key="source">{{ source }}</li>
          </ul>
        </div>
      </div>

      <!--  -->
      <div class="note">
         <strong></strong><br>
        1. <br>
        2.  Hugging Face  1-3 
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue';

export default {
  name: 'AskView',

  setup() {
    // API 
    const API_URL = 'https://kevin7261-gisgym.hf.space/ask_with_zip';

    // 
    const selectedFile = ref(null);
    const questionText = ref('');
    const isProcessing = ref(false);
    const statusMessage = ref('');
    const statusType = ref(''); // 'success', 'error', 'info'
    const isDragOver = ref(false);
    const resultQuestion = ref('');
    const resultAnswer = ref('');
    const resultSources = ref([]);

    // 
    const uploadText = computed(() => {
      return isDragOver.value ? '' : ' ZIP ';
    });

    const buttonText = computed(() => {
      return isProcessing.value ? '...' : '';
    });

    const statusClass = computed(() => {
      return `status-${statusType.value}`;
    });

    const clearResult = () => {
      resultQuestion.value = '';
      resultAnswer.value = '';
      resultSources.value = [];
      statusMessage.value = '';
    };

    // 
    const handleFileSelect = (event) => {
      const files = event.target.files;
      if (files && files.length > 0) {
        selectedFile.value = files[0];
        clearResult();
      }
    };

    // 
    const handleDragOver = () => {
      isDragOver.value = true;
    };

    const handleDragLeave = () => {
      isDragOver.value = false;
    };

    const handleDrop = (event) => {
      isDragOver.value = false;
      const files = event.dataTransfer.files;
      if (files && files.length > 0) {
        selectedFile.value = files[0];
        clearResult();
      }
    };

    // 
    const submitQuestion = async () => {
      if (!selectedFile.value) {
        alert(' rag_db.zip ');
        return;
      }

      if (!questionText.value) {
        alert('');
        return;
      }

      // 
      const file = selectedFile.value;
      if (file.type !== 'application/zip' && !file.name.endsWith('.zip')) {
        alert(' .zip ');
        return;
      }

      // 
      clearResult();
      isProcessing.value = true;
      statusType.value = 'info';
      statusMessage.value = ' ...';

      try {
        const formData = new FormData();
        formData.append('file', selectedFile.value);
        formData.append('question', questionText.value);

        //  POST 
        const response = await fetch(API_URL, {
          method: 'POST',
          body: formData
        });

        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`: ${response.status} - ${errorText}`);
        }

        const data = await response.json();
        resultQuestion.value = data.question || questionText.value;
        resultAnswer.value = data.answer || '';
        resultSources.value = Array.isArray(data.sources) ? data.sources : [];

        statusType.value = 'success';
        statusMessage.value = '  AI ';
      } catch (error) {
        statusType.value = 'error';
        statusMessage.value = ` : ${error.message}`;
      } finally {
        isProcessing.value = false;
      }
    };

    return {
      selectedFile,
      questionText,
      isProcessing,
      statusMessage,
      statusClass,
      isDragOver,
      uploadText,
      buttonText,
      resultQuestion,
      resultAnswer,
      resultSources,
      handleFileSelect,
      handleDragOver,
      handleDragLeave,
      handleDrop,
      submitQuestion,
      clearResult
    };
  }
};
</script>

