/**
 *   (Icon Mapping Table)
 *
 *  FontAwesome 
 *  FontAwesome 
 *
 * 
 * - 
 * - 
 * -  FontAwesome 5 
 * - 
 *
 * @type {Object.<string, {zh: string, en: string, icon: string}>}
 * @description  HTML 
 *
 * @example
 * // 
 * {
 *   chart_line: {
 *     zh: '',
 *     en: 'Line Chart',
 *     icon: '<i class="fas fa-chart-line"></i>'
 *   }
 * }
 */
export const ICONS = {
  // 
  chart_line: { zh: '', en: 'Line Chart', icon: '<i class="fas fa-chart-line"></i>' },
  chart_bar: { zh: '', en: 'Bar Chart', icon: '<i class="fas fa-chart-bar"></i>' },

  // 
  spinner: { zh: '', en: 'Loading', icon: '<i class="fas fa-spinner"></i>' },

  // 
  sort: { zh: '', en: 'Sort', icon: '<i class="fas fa-sort"></i>' },
  sort_up: { zh: '', en: 'Sort Ascending', icon: '<i class="fas fa-sort-up"></i>' },
  sort_down: { zh: '', en: 'Sort Descending', icon: '<i class="fas fa-sort-down"></i>' },

  // 
  info_circle: { zh: '', en: 'Information', icon: '<i class="fas fa-info-circle"></i>' },
  location_dot: { zh: '', en: 'Location', icon: '<i class="fa-solid fa-location-dot"></i>' },
  map: { zh: '', en: 'Map', icon: '<i class="fas fa-map"></i>' },
  map_marked: { zh: '', en: 'Marked Map', icon: '<i class="fas fa-map-marked-alt"></i>' },

  // 
  layer_group: { zh: '', en: 'Layer Group', icon: '<i class="fas fa-layer-group"></i>' },
  table: { zh: '', en: 'Table', icon: '<i class="fas fa-table"></i>' },
  json_data: { zh: '', en: 'Raw Data', icon: '<i class="fas fa-database"></i>' },
  draw_data: { zh: '', en: 'Draw Data', icon: '<i class="fas fa-palette"></i>' },
  code: { zh: '', en: 'Code', icon: '<i class="fas fa-code"></i>' },

  // 
  project_diagram: { zh: '', en: 'Network Diagram', icon: '<i class="fas fa-project-diagram"></i>' },
  th: { zh: '', en: 'Grid', icon: '<i class="fas fa-th"></i>' },
  file_code: { zh: '', en: 'Code File', icon: '<i class="fas fa-file-code"></i>' },

  // 
  work: { zh: '', en: 'Work', icon: '<i class="fas fa-briefcase"></i>' },
};

/**
 *   (Get Icon Information by Key)
 *
 * 
 * 
 *
 * 
 * - 
 * -  FontAwesome 
 * - 
 * - 
 *
 * @param {string} iconKey -  ICONS 
 * @param {string} [lang='zh'] -  'zh' 'en'
 * @returns {Object} 
 * @returns {string} returns.text - 
 * @returns {string} returns.icon - FontAwesome 
 *
 * @description 
 *
 * @example
 * // 
 * const chartIcon = getIcon('chart_line');
 * // : { text: '', icon: 'fas fa-chart-line' }
 *
 * // 
 * const chartIconEn = getIcon('chart_line', 'en');
 * // : { text: 'Line Chart', icon: 'fas fa-chart-line' }
 *
 * // 
 * const unknownIcon = getIcon('unknown_icon');
 * // : { text: 'unknown_icon', icon: 'fas fa-question-circle' }
 */
export function getIcon(iconKey, lang = 'zh') {
  const iconInfo = ICONS[iconKey];
  if (!iconInfo) {
    // console.warn(`: ${iconKey}`);
    return {
      text: iconKey,
      icon: 'fas fa-question-circle',
    };
  }

  //  HTML  class 
  const classMatch = iconInfo.icon.match(/class="([^"]+)"/);
  const iconClass = classMatch ? classMatch[1] : 'fas fa-question-circle';

  return {
    text: iconInfo[lang] || iconInfo.zh,
    icon: iconClass,
  };
}

/**
 *   HTML (Get Complete Icon HTML by Key)
 *
 *  HTML 
 *  CSS 
 *
 * 
 * -  HTML  DOM
 * -  CSS 
 * - 
 * -  HTML 
 *
 * @param {string} iconKey -  ICONS 
 * @param {string} [additionalClasses=''] -  CSS  class 
 * @returns {string}  HTML 
 *
 * @description  HTML
 *
 * @example
 * //  HTML
 * const chartIconHtml = getIconHtml('chart_line');
 * // : '<i class="fas fa-chart-line"></i>'
 *
 * //  HTML
 * const spinnerHtml = getIconHtml('spinner', 'fa-spin me-2');
 * // : '<i class="fas fa-spinner fa-spin me-2"></i>'
 *
 * //  HTML
 * const customIcon = getIconHtml('info_circle', 'text-primary fs-4');
 * // : '<i class="fas fa-info-circle text-primary fs-4"></i>'
 *
 * // 
 * const unknownIcon = getIconHtml('unknown_icon', 'text-danger');
 * // : '<i class="fas fa-question-circle text-danger"></i>'
 */
export function getIconHtml(iconKey, additionalClasses = '') {
  const iconInfo = ICONS[iconKey];
  if (!iconInfo) {
    // console.warn(`: ${iconKey}`);
    return `<i class="fas fa-question-circle${additionalClasses ? ' ' + additionalClasses : ''}"></i>`;
  }

  //  class 
  if (additionalClasses) {
    return iconInfo.icon.replace(/class="([^"]+)"/, `class="$1 ${additionalClasses}"`);
  }

  return iconInfo.icon;
}

// ====================   (Clipboard Utility Functions) ====================

/**
 *   (Copy Text to Clipboard)
 *
 * 
 *  Clipboard API
 *
 * 
 * -  navigator.clipboard API
 * -  JSON 
 * - 
 * - 
 * - 
 *
 * @param {string|Object} data - 
 * @param {Object} [options] - 
 * @param {string} [options.successMessage=' '] - 
 * @param {string} [options.errorMessage=' '] - 
 * @param {number} [options.clearDelay=3000] - 0 
 * @param {Function} [options.onSuccess] - 
 * @param {Function} [options.onError] - 
 * @returns {Promise<Object>} 
 * @returns {boolean} returns.success - 
 * @returns {string} returns.message - 
 * @returns {string} returns.data - 
 *
 * @description  JSON 
 *
 * @example
 * // 
 * const result = await copyToClipboard('Hello World');
 * console.log(result.message); // ' '
 *
 * //  JSON 
 * const jsonData = { name: 'test', value: 123 };
 * const result = await copyToClipboard(jsonData);
 *
 * // 
 * const result = await copyToClipboard(data, {
 *   successMessage: ' ',
 *   errorMessage: ' ',
 *   clearDelay: 5000,
 *   onSuccess: (message) => console.log(':', message),
 *   onError: (error) => console.error(':', error)
 * });
 */
export async function copyToClipboard(data, options = {}) {
  const {
    successMessage = ' ',
    errorMessage = ' ',
    onSuccess,
    onError,
  } = options;

  try {
    // 
    let textToCopy;
    if (typeof data === 'string') {
      textToCopy = data;
    } else if (typeof data === 'object' && data !== null) {
      textToCopy = JSON.stringify(data, null, 2);
    } else {
      textToCopy = String(data);
    }

    // 
    if (!textToCopy || textToCopy.trim() === '') {
      const errorResult = {
        success: false,
        message: ' ',
        data: textToCopy,
      };
      if (onError) onError(new Error(''));
      return errorResult;
    }

    //  Clipboard API
    if (navigator.clipboard && window.isSecureContext) {
      await navigator.clipboard.writeText(textToCopy);
    } else {
      //  document.execCommand
      const textArea = document.createElement('textarea');
      textArea.value = textToCopy;
      textArea.style.position = 'fixed';
      textArea.style.left = '-999999px';
      textArea.style.top = '-999999px';
      document.body.appendChild(textArea);
      textArea.focus();
      textArea.select();

      const successful = document.execCommand('copy');
      document.body.removeChild(textArea);

      if (!successful) {
        throw new Error('document.execCommand ');
      }
    }

    const successResult = {
      success: true,
      message: successMessage,
      data: textToCopy,
    };

    if (onSuccess) onSuccess(successResult);
    return successResult;
  } catch (error) {
    console.error(':', error);
    const errorResult = {
      success: false,
      message: errorMessage,
      data: typeof data === 'string' ? data : JSON.stringify(data, null, 2),
    };
    if (onError) onError(error);
    return errorResult;
  }
}

/**
 *   (Create Copy Function with State Management)
 *
 *  Vue 
 * 
 *
 * 
 * - 
 * - 
 * -  Vue 
 * - 
 *
 * @param {Object} stateRef - Vue 
 * @param {string} [stateKey='copyMessage'] - 
 * @param {Object} [options] - 
 * @returns {Function} 
 *
 * @example
 * //  Vue 
 * import { ref } from 'vue';
 * import { createCopyFunction } from '@/utils/utils.js';
 *
 * const copyMessage = ref('');
 * const copyFunction = createCopyFunction({ copyMessage });
 *
 * // 
 * await copyFunction(jsonData);
 */
export function createCopyFunction(stateRef, stateKey = 'copyMessage', options = {}) {
  return async (data) => {
    const result = await copyToClipboard(data, {
      ...options,
      onSuccess: (result) => {
        stateRef[stateKey] = result.message;
        if (options.clearDelay !== 0) {
          setTimeout(() => {
            stateRef[stateKey] = '';
          }, options.clearDelay || 3000);
        }
        if (options.onSuccess) options.onSuccess(result);
      },
      onError: (error) => {
        stateRef[stateKey] = error.message || ' ';
        if (options.clearDelay !== 0) {
          setTimeout(() => {
            stateRef[stateKey] = '';
          }, options.clearDelay || 3000);
        }
        if (options.onError) options.onError(error);
      },
    });
    return result;
  };
}
