// ====================   (Third-Party Library Imports) ====================

/**
 * Vue Router 4 
 * 
 *
 * @see https://router.vuejs.org/
 */
import { createRouter, createWebHistory } from 'vue-router';

/**
 * 
 * 
 *
 * @see ../views/HomeView.vue
 */
import HomeView from '../views/HomeView.vue';

/**
 * RAG 
 * PDF 
 *
 * @see ../views/RagView.vue
 */
import RagView from '../views/RagView.vue';
import AskView from '../views/AskView.vue';

// ====================   (Route Configuration Definition) ====================

/**
 *   (Routes Configuration Array)
 *
 * 
 * 
 *
 * 
 * - 
 * - 
 * - 
 * - 
 *
 * @type {Array<Object>}
 * @description 
 */
const routes = [
  {
    path: '/', //  
    name: 'Home', // 
    component: HomeView, //  Vue 
    meta: {
      title: 'GISGym', // 
      description: 'GISGym', // 
      requiresAuth: false, // 
    },
  },
  {
    path: '/rag', //  RAG 
    name: 'Rag', // 
    component: RagView, //  Vue 
    meta: {
      title: 'PDF  (RAG)', // 
      description: 'PDF  FAISS ', // 
      requiresAuth: false, // 
    },
  },
  {
    path: '/ask', //  
    name: 'Ask', // 
    component: AskView, //  Vue 
    meta: {
      title: ' RAG ', // 
      description: ' rag_db.zip  AI ', // 
      requiresAuth: false, // 
    },
  },
  // 
  // {
  //   path: '/about',
  //   name: 'About',
  //   component: () => import('../views/AboutView.vue'),
  //   meta: {
  //     title: '',
  //     description: '',
  //     requiresAuth: false,
  //   },
  // },
];

// ====================   (Router Instance Creation) ====================

/**
 *   (Router Instance Creation)
 *
 *  Vue Router 4 
 *  HTML5 History API  URL 
 *
 * 
 * - history:  HTML5 History API 
 * - base:  GitHub Pages 
 * - routes: 
 * - scrollBehavior: 
 *
 * @type {Router}
 * @description Vue Router 
 */
const router = createRouter({
  /**
   *  HTML5 History API 
   *  URL  # 
   *  index.html
   */
  history: createWebHistory('/gisgym/'),

  /**
   * 
   * 
   */
  routes,

  /**
   * 
   * 
   */
  scrollBehavior(to, from, savedPosition) {
    // 
    if (savedPosition) {
      return savedPosition;
    }
    // 
    return { top: 0 };
  },
});

// ====================   (Route Guards Configuration) ====================

/**
 *   (Global Before Guard)
 *
 * 
 * 
 *
 * @param {RouteLocationNormalized} to - 
 * @param {RouteLocationNormalized} from - 
 * @param {Function} next - 
 */
router.beforeEach((to, from, next) => {
  // 
  if (to.meta.title) {
    document.title = `${to.meta.title} - GISGym`;
  } else {
    document.title = 'GISGym';
  }

  // 
  // 
  next();
});

/**
 *   (Global After Guard)
 *
 * 
 *  Google Analytics
 *
 * @param {RouteLocationNormalized} to - 
 * @param {RouteLocationNormalized} from - 
 */
router.afterEach(() => {
  // 
  // 
  // Google Analytics 
  // gtag('config', 'GA_MEASUREMENT_ID', {
  //   page_path: to.fullPath,
  // });
});

// ====================   (Module Export) ====================

/**
 * 
 *  main.js  Vue 
 */
export default router;
