/** *   (Layer Information Display Component) * *  (Function Overview): *
 *
 * *  (Main Features): * 1. 
 * 2. 
 * 3. 
 * 4.   * 5. 
 * 6.   D3.js  * *
 (Technical Features): * -  Vue 3 Composition API  * -  Pinia
 * -  * - 
* -  * *  (Display Content): * -
 * -  * -
 * - D3.js  * -
 * * @file LayerInfoTab.vue * @version 2.1.0 * @author Kevin Cheng *
@since 1.0.0 * @updated 2024 -  */
<script setup>
  // ====================   (Third-Party Library Imports) ====================

  /**
   * Vue 3 Composition API 
   *  Vue 
   *
   * @description 
   * - ref: 
   * - computed: 
   * - watch: 
   * - onMounted: 
   *
   * @see https://vuejs.org/guide/extras/composition-api-faq.html
   */
  import { ref, computed, watch, onMounted } from 'vue';

  /**
   * Pinia 
   * 
   *
   * @description 
   * - 
   * - 
   * - 
   * - 
   *
   * @see https://pinia.vuejs.org/introduction.html
   */
  import { useDataStore } from '@/stores/dataStore.js';

  /**
   * 
   *  HTML 
   */
  import { getIconHtml } from '../utils/utils.js';

  // ====================   (State Management Initialization) ====================

  /**
   *  Pinia 
   * 
   *
   * @type {Object} Pinia store 
   * @description 
   */
  const dataStore = useDataStore();

  // ====================   (Reactive State Definition) ====================

  /**
   *   (Active Layer Tab)
   * 
   *
   * @type {Ref<string|null>}
   * @description
   * -  layerId
   * - null 
   * - 
   */
  const activeLayerTab = ref(null);

  /**
   *   (Analysis Results)
   * 
   *
   * @type {Ref<Object|null>}
   * @description
   * - 
   * - null 
   * -  layerName, timestamp, statistics 
   */
  const analysisResults = ref(null);

  /**
   *   (Analysis Loading State)
   * 
   *
   * @type {Ref<boolean>}
   * @description
   * - true: 
   * - false: 
   */
  const isLoadingAnalysis = ref(false);


  // ====================   (Computed Properties Definition) ====================

  /**
   *  (Get All Visible Layers with Data)
   * 
   *
   * @type {ComputedRef<Array>}
   * @description
   * - 
   * - 
   * -  layerId, layerName, dataTableData 
   * - 
   *
   * @returns {Array<Object>} 
   */
  const visibleLayers = computed(() => {
    // 
    const allLayers = dataStore.getAllLayers();
    // layer.visible === true
    return allLayers.filter((layer) => layer.visible);
  });


  // ====================   (Core Function Functions) ====================

  /**
   *   (Set Active Layer Tab)
   * 
   *
   * @param {string} layerId -  ID
   * @description  activeLayerTab 
   */
  const setActiveLayerTab = (layerId) => {
    activeLayerTab.value = layerId;
  };

  /**
   *   () (Get Layer Full Title with Group Name)
   * 
   *
   * @param {Object} layer - 
   * @returns {Object}  groupName  layerName 
   * @description
   * -  dataStore 
   * - 
   * - 
   */
  const getLayerFullTitle = (layer) => {
    if (!layer) return { groupName: null, layerName: '' };
    const groupName = dataStore.findGroupNameByLayerId(layer.layerId);
    return {
      groupName: groupName,
      layerName: layer.layerName,
    };
  };

  /**
   *   (Format Display Value)
   *  [object Object]
   *
   * @param {any} value - 
   * @returns {string} 
   * @description
   * - 
   * - 
   * -  JSON 
   * -  null/undefined
   */
  const formatDisplayValue = (value) => {
    //  null  undefined
    if (value === null || value === undefined) {
      return '';
    }

    // 
    if (typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean') {
      return String(value);
    }

    // 
    if (Array.isArray(value)) {
      if (value.length === 0) {
        return '';
      }

      // 
      const hasObjects = value.some((item) => typeof item === 'object' && item !== null);

      if (hasObjects) {
        // 
        return value
          .map((item, index) => {
            if (typeof item === 'object' && item !== null) {
              // 
              const keys = Object.keys(item);
              if (keys.length > 0) {
                const mainKey = keys[0];
                return `${index + 1}: ${mainKey}=${item[mainKey]}`;
              }
              return `${index + 1}: `;
            }
            return String(item);
          })
          .join(', ');
      } else {
        // 
        return value.join(', ');
      }
    }

    // 
    if (typeof value === 'object') {
      const keys = Object.keys(value);
      if (keys.length === 0) {
        return '';
      }

      // 
      if (keys.length <= 3) {
        return keys.map((key) => `${key}: ${value[key]}`).join(', ');
      }

      // 
      const previewKeys = keys.slice(0, 2);
      return (
        previewKeys.map((key) => `${key}: ${value[key]}`).join(', ') +
        ` ... ( ${keys.length} )`
      );
    }

    // 
    return String(value);
  };

  /**
   * 
   *  dataStore.js 
   * Data  function 
   */
  const currentLayerConfigEntries = computed(() => {
    if (!activeLayerTab.value) return [];
    const currentLayer = visibleLayers.value.find(
      (layer) => layer.layerId === activeLayerTab.value
    );
    if (!currentLayer) return [];

    //  dataStore.js 
    const fieldsOrder = [
      'layerId',
      'layerName',
      'visible',
      'isLoading',
      'isLoaded',
      'colorName',
      'jsonData',
      'geojsonData',
      'processedJsonData',
      'drawJsonData',
      'dashboardData',
      'dataTableData',
      'layerInfoData',
      'jsonLoader',
      'geojsonLoader',
      'processToDrawData',
      'geojsonFileName',
      'jsonFileName',
      'executeFunction',
      'display',
      'upperViewTabs',
    ];

    // Data  function 
    const dataAndFunctionFields = new Set([
      'jsonData',
      'geojsonData',
      'processedJsonData',
      'drawJsonData',
      'dashboardData',
      'dataTableData',
      'layerInfoData',
      'jsonLoader',
      'geojsonLoader',
      'processToDrawData',
      'executeFunction',
    ]);

    return fieldsOrder
      .filter((key) => key in currentLayer)
      .map((key) => {
        let value = currentLayer[key];
        // Data  function VX
        if (dataAndFunctionFields.has(key)) {
          value = value !== null && value !== undefined ? 'V' : 'X';
        } else if (typeof value === 'function') {
          value = value.name || '';
        }
        return [key, value];
      });
  });

  // ====================   (Reactive Watchers) ====================

  /**
   * 
   * 
   */
  const previousLayers = ref([]);

  /**
   *  
   * 
   *
   * @description 
   * - 
   * - 
   * - 
   * - 
   */
  watch(
    () => visibleLayers.value,
    (newLayers) => {
      // 
      if (newLayers.length === 0) {
        activeLayerTab.value = null;
        analysisResults.value = null;
        previousLayers.value = [];
        return;
      }

      // 
      const previousLayerIds = previousLayers.value.map((layer) => layer.layerId);
      const newLayerIds = newLayers.map((layer) => layer.layerId);
      const addedLayerIds = newLayerIds.filter((id) => !previousLayerIds.includes(id));

      // 
      if (addedLayerIds.length > 0) {
        const newestAddedLayerId = addedLayerIds[addedLayerIds.length - 1];
        activeLayerTab.value = newestAddedLayerId;
      }
      // 
      else if (
        !activeLayerTab.value ||
        !newLayers.find((layer) => layer.layerId === activeLayerTab.value)
      ) {
        activeLayerTab.value = newLayers[0].layerId;
      }

      // 
      previousLayers.value = [...newLayers];
    },
    { deep: true, immediate: true }
  );

  // ====================   (Data Processing Functions) ====================

  /**
   *   (Load Layer Basic Information)
   * 
   *
   * @param {Object} layer - 
   * @description 
   * -  features 
   * - 
   * - 
   * -  analysisResults 
   */
  const loadLayerInfo = async (layer) => {
    //  geojsonData processedJsonData
    const hasData = layer && (layer.geojsonData || layer.processedJsonData);
    if (!hasData) {
      console.warn('');
      return;
    }

    isLoadingAnalysis.value = true;

    try {
      // 
      await new Promise((resolve) => setTimeout(resolve, 500));

      //  geojsonData processedJsonData
      const geojsonData =
        layer.geojsonData ||
        (layer.processedJsonData && layer.processedJsonData.features
          ? layer.processedJsonData
          : null);
      if (!geojsonData || !geojsonData.features) {
        throw new Error(' GeoJSON ');
      }

      const features = geojsonData.features;

      // 
      const stats = {
        totalFeatures: features.length,
        totalPopulation: features.reduce((sum, f) => sum + (f.properties.P_CNT || 0), 0),
        totalCount: features.reduce((sum, f) => sum + (f.properties.count || 0), 0),
        avgPopulation: 0,
        avgCount: 0,
      };

      // 
      if (stats.totalFeatures > 0) {
        stats.avgPopulation = stats.totalPopulation / stats.totalFeatures;
        stats.avgCount = stats.totalCount / stats.totalFeatures;
      }

      // 
      analysisResults.value = {
        layerName: layer.layerName,
        timestamp: new Date().toLocaleString(),
        statistics: stats,
      };
    } catch (error) {
      console.error(':', error);
      // 
      analysisResults.value = {
        error: '',
        details: error.message,
      };
    } finally {
      isLoadingAnalysis.value = false;
    }
  };

  /**
   *  
   *  activeLayerTab 
   *
   * @description 
   * - 
   * - 
   * - 
   */
  watch(
    () => activeLayerTab.value,
    (newLayerId) => {
      if (newLayerId) {
        const layer = dataStore.findLayerById(newLayerId);
        if (layer && layer.processedJsonData) {
          loadLayerInfo(layer);
        }
      } else {
        analysisResults.value = null;
      }
    },
    { immediate: true }
  );

  /**
   *   processedJsonData 
   * 
   */
  watch(
    () => {
      if (!activeLayerTab.value) return null;
      const layer = visibleLayers.value.find((l) => l.layerId === activeLayerTab.value);
      return layer ? layer.processedJsonData : null;
    },
    (newProcessedData) => {
      if (newProcessedData) {
        // 
        // Vue 
      }
    },
    { deep: true }
  );

  /**
   *   layoutGridJsonData/spaceNetworkGridJsonData/jsonData 
   * 
   * taipei  jsonData
   */
  watch(
    () => {
      if (!activeLayerTab.value) return null;
      const layer = visibleLayers.value.find((l) => l.layerId === activeLayerTab.value);
      if (!layer) return null;
      const isTaipeiLayer = layer.layerId?.startsWith('taipei_');
      return isTaipeiLayer
        ? layer.layoutGridJsonData || layer.spaceNetworkGridJsonData
        : layer.layoutGridJsonData || layer.spaceNetworkGridJsonData || layer.jsonData;
    },
    () => {
      // 
      //  currentLayerConfigEntries  computed 
    },
    { deep: true, immediate: true }
  );

  // ====================   (Lifecycle Hooks) ====================


  /**
   *   (Component Mounted Event)
   * 
   *
   * @description 
   * - 
   * - 
   */
  onMounted(() => {
    // 
    if (visibleLayers.value.length > 0 && !activeLayerTab.value) {
      activeLayerTab.value = visibleLayers.value[0].layerId;
    }
  });
</script>

<template>
  <!--   -->
  <div class="d-flex flex-column my-bgcolor-gray-200 h-100">
    <!--   -->
    <div v-if="visibleLayers.length > 0" class="">
      <ul class="nav nav-tabs nav-fill">
        <li
          v-for="layer in visibleLayers"
          :key="layer.layerId"
          class="nav-item d-flex flex-column align-items-center"
        >
          <!-- tab -->
          <div
            class="btn nav-link rounded-0 border-0 position-relative d-flex align-items-center justify-content-center my-bgcolor-gray-200"
            :class="{
              active: activeLayerTab === layer.layerId,
            }"
            @click="setActiveLayerTab(layer.layerId)"
          >
            <span>
              <span v-if="getLayerFullTitle(layer).groupName" class="my-title-xs-gray"
                >{{ getLayerFullTitle(layer).groupName }} -
              </span>
              <span class="my-title-sm-black">{{ getLayerFullTitle(layer).layerName }}</span>
            </span>
          </div>
          <div class="w-100" :class="`my-bgcolor-${layer.colorName}`" style="min-height: 4px"></div>
        </li>
      </ul>
    </div>

    <!--  -->
    <div v-if="visibleLayers.length > 0" class="my-bgcolor-white h-100">
      <div>
        <div class="p-3">
          <!--  -->
          <div v-if="isLoadingAnalysis" class="pb-2">
            <div class="my-title-xs-gray pb-1"></div>
            <div class="my-content-sm-black pb-1">
              <span v-html="getIconHtml('spinner', 'fa-spin me-2')"></span>
              ...
            </div>
          </div>

          <!--  -->
          <template v-if="visibleLayers.length > 0 && currentLayerConfigEntries.length > 0">
            <!--  dataStore.js Data  function  -->
            <div v-if="currentLayerConfigEntries.length > 0">
              <div v-for="[key, value] in currentLayerConfigEntries" :key="key" class="pb-2">
                <div class="my-title-xs-gray pb-1">{{ key }}</div>
                <div class="my-content-sm-black pb-1">
                  {{ formatDisplayValue(value) }}
                </div>
              </div>
            </div>
          </template>

          <!--  -->
          <div v-else-if="analysisResults && analysisResults.error" class="pb-2">
            <div class="my-title-xs-gray pb-1"></div>
            <div class="my-content-sm-black pb-1">{{ analysisResults.error }}</div>
            <div v-if="analysisResults.details" class="my-content-xs-gray pb-1">
              {{ analysisResults.details }}
            </div>
          </div>

          <!--  -->
          <div v-else-if="!isLoadingAnalysis" class="pb-2">
            <div class="my-title-xs-gray pb-1"></div>
            <div class="my-content-sm-black pb-1">...</div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-else class="flex-grow-1 d-flex align-items-center justify-content-center">
      <div class="text-center">
        <div class="my-title-md-gray p-3"></div>
      </div>
    </div>
  </div>
</template>

<style scoped></style>
