2.   * 3.   * 4.
  * 5.   * *
 (Technical Features): * -  Vue 2 Options API  * -  Pinia  *
-  * -  * -  * *
 (Included Tabs): * - LayerInfo * - ControlTab * * @file RightView.vue * @version 2.0.0 * @author
Kevin Cheng * @since 1.0.0 */
<script>
  // ====================   (Third-Party Library Imports) ====================


  /**
   * 
   * 
   *
   * @see ../tabs/LayerInfoTab.vue
   */
  import LayerInfo from '../tabs/LayerInfoTab.vue';

  /**
   * 
   * 
   *
   * @see ../tabs/ControlTab.vue
   */
  import ControlTab from '../tabs/ControlTab.vue';

  /**
   * Pinia 
   * 
   *
   * @see ../stores/dataStore.js
   */
  import { useDataStore } from '../stores/dataStore';

  /**
   * Vue Composition API 
   * 
   *
   * @see https://vuejs.org/
   */
  import { computed, watch } from 'vue';
  import { getIcon } from '../utils/utils.js';

  export default {
    name: 'RightView',
    components: {
      LayerInfo, // 
      ControlTab, // 
    },
    props: {
      /**   */
      activeRightTab: {
        type: String,
        default: 'layer-info',
      },
      /**   */
      analysisList: {
        type: Array,
        default: () => [],
      },
      /**   ID */
      selectedAnalysisId: {
        type: [Number, String],
        default: null,
      },
      /**   () */
      rightViewWidth: {
        type: Number,
        default: 250,
      },
    },

    /**
     *   (Component Events)
     * 
     */
    emits: [
      'update:activeRightTab', // 
      'select-analysis', // 
      'view-analysis', // 
      'delete-analysis', // 
      'highlight-feature', // 
    ],

    /**
     *   (Component Setup)
     *  Composition API 
     */
    setup(props, { emit }) {
      //   Pinia 
      const dataStore = useDataStore();

      //  
      const availableTabs = [
        { id: 'layer-info', name: '', icon: getIcon('info_circle').icon },
        { id: 'control', name: '', icon: getIcon('play').icon },
      ];

      //  
      const switchTab = (tabId) => {
        emit('update:activeRightTab', tabId);
      };

      /**
       *   (Calculate Active Analysis Count)
       * 
       *
       * @returns {number} 
       */
      const getActiveAnalysisCount = () => {
        return props.analysisList.filter((a) => a.status === '').length;
      };

      /**
       *   (Watch Selected Feature Changes)
       *  Pinia store  selectedFeature 
       */
      watch(
        () => dataStore.selectedFeature,
        () => {
          // Feature changed
        },
        { immediate: true }
      ); // immediate: true 

      /**
       *   (Selected Feature Computed Property)
       *  Pinia store 
       * 
       */
      const selectedFeatureComputed = computed(() => {
        const feature = dataStore.selectedFeature;

        return feature;
      });

      //  
      return {
        availableTabs,
        switchTab,
        getActiveAnalysisCount, // 
        selectedFeature: selectedFeatureComputed, // 
      };
    },
  };
</script>

<template>
  <div class="my-right-panel h-100 d-flex flex-column overflow-hidden">
    <!--   -->
    <div class="d-flex justify-content-center my-bgcolor-gray-200 p-3">
      <div class="d-flex align-items-center rounded-pill shadow my-blur gap-2 p-2 w-100">
        <button
          v-for="tab in availableTabs"
          :key="tab.id"
          class="btn rounded-pill border-0 my-btn-transparent my-font-size-xs text-nowrap w-100 my-cursor-pointer"
          :class="{
            'my-btn-blue': $props.activeRightTab === tab.id,
          }"
          @click="switchTab(tab.id)"
          :title="tab.name"
        >
          {{ tab.name }}
        </button>
      </div>
    </div>

    <!--   -->
    <div class="flex-grow-1 overflow-auto">
      <!--   -->
      <div v-show="$props.activeRightTab === 'layer-info'" class="h-100">
        <LayerInfo />
      </div>

      <!--   -->
      <div v-show="$props.activeRightTab === 'control'" class="h-100">
        <ControlTab />
      </div>
    </div>
  </div>
</template>

<style scoped></style>
