/** *  LoadingOverlay.vue -  (Loading Overlay Component) * *
 *
 * *  
(Core Features): * 1.   * -
 * -  * -
 * 2.   * - Bootstrap 
UI  * -  * -  * 3. 
 * -  * -  * -
 * 4.   * -  * -
 * -  * 5.   * -
/ * -  * -  * *   (Technical
Features): * - Vue 3  * - Bootstrap 5  UI  * -
 * -  * -  * *  
(Visual Design): * -  * -  * -
 * -  * *   (Use Cases): * -
 * -  * -  * -  * -
 * -  * *   (Responsive Support): * -
 * -  * -  * * 
 API (Component API): * - isVisible: / * - loadingText:  * -
progress:  (0-100) * - showProgress:  * - subText:  * *
@component LoadingOverlay * @version 2.0.0 * @author Kevin Cheng * @since 1.0.0 * @see {@link
https://getbootstrap.com/docs/5.0/components/spinners/} Bootstrap  */
<script>
  // ====================   (Component Definition) ====================

  //  Vue 
  export default {
    /**
     * 
     *  Vue DevTools 
     * - name: 'LoadingOverlay' - 
     */
    name: 'LoadingOverlay',

    /**
     *  (Component Props)
     * 
     * 
     */
    props: {
      /**
       * 
       * - type: Boolean - 
       * - default: false - 
       * - required: true - 
       */
      isVisible: {
        type: Boolean, // 
        default: false, // 
        required: true, // 
      },

      /**
       * 
       * - type: String - 
       * - default: '...' - 
       * - 
       */
      loadingText: {
        type: String, // 
        default: '...', // 
      },

      /**
       * 
       * - type: Number - 
       * - default: -1 -  -1 
       * - validator:  -1  100 
       * - 0-100: 
       * - -1: 
       */
      progress: {
        type: Number, // 
        default: -1, // -1 
        // 
        validator: (value) => value >= -1 && value <= 100,
      },

      /**
       * 
       * - type: Boolean - 
       * - default: false - 
       * - 
       */
      showProgress: {
        type: Boolean, // 
        default: false, // 
      },

      /**
       * 
       * - type: String - 
       * - default: '' - 
       * - 
       */
      subText: {
        type: String, // 
        default: '', // 
      },
    },
  };
</script>

<template>
  <!--  -->
  <!--  -->

  <!--
      (Full Screen Overlay Container)
    - class="position-fixed": Bootstrap 5 
    - class="top-0 start-0": top: 0, left: 0
    - class="w-100 h-100":  100%
    - class="d-flex":  flexbox 
    - class="justify-content-center": 
    - class="align-items-center": 
    - style="background-color: rgba(0, 0, 0, 0.7)":  0.7
    - style="z-index: 9999": 
    - v-if="isVisible": Vue  isVisible  true 
  -->
  <div
    class="position-fixed top-0 start-0 w-100 h-100 d-flex justify-content-center align-items-center"
    style="background-color: rgba(0, 0, 0, 0.7); z-index: 9999"
    v-if="isVisible"
  >
    <!--
        (Loading Content Card)
      - class="text-center": 
      - class="my-bgcolor-white": 
      - class="p-4": Bootstrap  1.5rem 
      - class="rounded": Bootstrap 
      - class="shadow": Bootstrap 
      - style="min-width: 300px":  300px
      - style="max-width: 400px":  400px
    -->
    <div
      class="text-center my-bgcolor-white p-4 rounded shadow"
      style="min-width: 300px; max-width: 400px"
    >
      <!--
          (Loading Spinner)
        - class="spinner-border": Bootstrap 5 
        - class="text-primary": 
        - class="mb-3": Bootstrap  1rem 
        - style="width: 2rem; height: 2rem":  2rem x 2rem
        - role="status": 
      -->
      <div class="spinner-border text-primary mb-3" style="width: 2rem; height: 2rem" role="status">
        <!--
            (Accessibility Screen Reader Text)
          - class="visually-hidden": Bootstrap 
          - "..." - 
        -->
        <span class="visually-hidden">...</span>
      </div>

      <!--
          (Main Loading Text)
        - class="my-title-lg-black": 
        - {{ loadingText }}: Vue  props  loadingText 
      -->
      <div class="my-title-lg-black">{{ loadingText }}</div>

      <!--
          (Loading Progress Bar Area)
        - class="mt-3": Bootstrap  1rem 
        - v-if="showProgress && progress >= 0": Vue 
          - showProgress: 
          - progress >= 0:  0
      -->
      <div class="mt-3" v-if="showProgress && progress >= 0">
        <!--
           Bootstrap  (Bootstrap Progress Container)
          - class="progress": Bootstrap 
          - style="height: 8px":  8px
        -->
        <div class="progress" style="height: 8px">
          <!--
              (Progress Bar Slider)
            - class="progress-bar": Bootstrap 
            - class="bg-primary": 
            - class="d-flex align-items-center justify-content-center": flexbox 
            - role="progressbar": 
            - :style="{ width: progress + '%' }": Vue  progress 
            - :aria-valuenow="progress": 
            - aria-valuemin="0":  0
            - aria-valuemax="100":  100
            - style="transition: width 0.3s ease": CSS 
            - style="font-size: 0.75rem; color: white": 
            - {{ Math.round(progress) }}%: 
          -->
          <div
            class="progress-bar bg-primary d-flex align-items-center justify-content-center"
            role="progressbar"
            :style="{ width: progress + '%' }"
            :aria-valuenow="progress"
            aria-valuemin="0"
            aria-valuemax="100"
            style="transition: width 0.3s ease; font-size: 0.75rem; color: white"
          >
            {{ Math.round(progress) }}%
          </div>
        </div>
      </div>

      <!--
          (Additional Description Text)
        - v-if="subText": Vue  subText 
        - class="mt-2": Bootstrap  0.5rem 
        - class="my-content-xs-gray": 
        - {{ subText }}: Vue  props  subText 
      -->
      <div v-if="subText" class="mt-2">
        <small class="my-content-xs-gray">{{ subText }}</small>
      </div>
    </div>
  </div>
</template>
