<script setup>
  /**
   *  WorkTab.vue -  (Work Tab Component)
   *
   * @component WorkTab
   * @version 1.0.0
   * @author Kevin Cheng
   * @since 1.0.0
   */

  // ====================   (Third-Party Library Imports) ====================
  import { ref, computed, watch, onMounted } from 'vue';
  import { useDataStore } from '@/stores/dataStore.js';

  // ====================   (State Management Initialization) ====================
  const dataStore = useDataStore();

  const activeLayerTab = ref(null); /**   */

  const emit = defineEmits(['active-layer-change']);

  // Props
  defineProps({
    containerHeight: {
      type: Number,
      default: 600,
    },
    isPanelDragging: {
      type: Boolean,
      default: false,
    },
    activeMarkers: {
      type: Number,
      default: 0,
    },
  });

  /**
   *   (Set Active Layer Tab)
   * @param {string} layerId -  ID
   */
  const setActiveLayerTab = (layerId) => {
    activeLayerTab.value = layerId;
    emit('active-layer-change', activeLayerTab.value);
  };

  // 
  const visibleLayers = computed(() => {
    const allLayers = dataStore.getAllLayers();
    return allLayers.filter((layer) => layer.visible);
  });

  // 
  const previousLayers = ref([]);

  /**
   *   (Current Layer Work Data)
   */
  const currentLayerWorkData = computed(() => {
    if (!activeLayerTab.value) return null;
    const layer = visibleLayers.value.find((l) => l.layerId === activeLayerTab.value);
    return layer ? layer.workData || null : null;
  });

  /**
   *   (Get Current Selected Layer Name)
   */
  const currentLayerName = computed(() => {
    if (!activeLayerTab.value) return '';
    const layer = visibleLayers.value.find((l) => l.layerId === activeLayerTab.value);
    return layer ? layer.layerName || '' : '';
  });

  /**
   *   () (Get Layer Full Title with Group Name)
   */
  const getLayerFullTitle = (layer) => {
    if (!layer) return { groupName: null, layerName: '' };
    const groupName = dataStore.findGroupNameByLayerId(layer.layerId);
    return {
      groupName: groupName,
      layerName: layer.layerName,
    };
  };

  /**
   *   (Format Display Value)
   * 
   */
  const formatDisplayValue = (value) => {
    if (value === null || value === undefined) {
      return '';
    }
    if (typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean') {
      return String(value);
    }
    if (Array.isArray(value)) {
      if (value.length === 0) {
        return '';
      }
      const hasObjects = value.some((item) => typeof item === 'object' && item !== null);
      if (hasObjects) {
        return value
          .map((item, index) => {
            if (typeof item === 'object' && item !== null) {
              const keys = Object.keys(item);
              if (keys.length > 0) {
                const mainKey = keys[0];
                return `${index + 1}: ${mainKey}=${item[mainKey]}`;
              }
              return `${index + 1}: `;
            }
            return String(item);
          })
          .join(', ');
      } else {
        return value.join(', ');
      }
    }
    if (typeof value === 'object') {
      const keys = Object.keys(value);
      if (keys.length === 0) {
        return '';
      }
      if (keys.length <= 3) {
        return keys.map((key) => `${key}: ${value[key]}`).join(', ');
      }
      const previewKeys = keys.slice(0, 2);
      return (
        previewKeys.map((key) => `${key}: ${value[key]}`).join(', ') +
        ` ... ( ${keys.length} )`
      );
    }
    return String(value);
  };

  /**
   *   workData 
   */
  const workDataEntries = computed(() => {
    if (!currentLayerWorkData.value) return [];
    return Object.entries(currentLayerWorkData.value);
  });

  /**
   *  
   */
  watch(
    () => visibleLayers.value,
    (newLayers) => {
      // 
      if (newLayers.length === 0) {
        activeLayerTab.value = null;
        previousLayers.value = [];
        return;
      }

      // 
      const previousLayerIds = previousLayers.value.map((layer) => layer.layerId);
      const newLayerIds = newLayers.map((layer) => layer.layerId);
      const addedLayerIds = newLayerIds.filter((id) => !previousLayerIds.includes(id));

      // 
      if (addedLayerIds.length > 0) {
        const newestAddedLayerId = addedLayerIds[addedLayerIds.length - 1];
        activeLayerTab.value = newestAddedLayerId;
        emit('active-layer-change', activeLayerTab.value);
      }
      // 
      else if (
        !activeLayerTab.value ||
        !newLayers.find((layer) => layer.layerId === activeLayerTab.value)
      ) {
        activeLayerTab.value = newLayers[0].layerId;
        emit('active-layer-change', activeLayerTab.value);
      }

      // 
      previousLayers.value = [...newLayers];
    },
    { deep: true, immediate: true }
  );

  /**
   *   (Component Mounted Event)
   */
  onMounted(() => {
    // 
    if (visibleLayers.value.length > 0 && !activeLayerTab.value) {
      activeLayerTab.value = visibleLayers.value[0].layerId;
      emit('active-layer-change', activeLayerTab.value);
    }
  });

  // ====================  AI  (AI Question Generation & Grading) ====================

  /**
   *  test_layer (Check if current layer is test_layer)
   */
  const isTestLayer = computed(() => {
    return activeLayerTab.value === 'test_layer';
  });

  // API  URL ( RagView  AskView )
  const API_BASE = ref('https://kevin7261-gisgym.hf.space');

  // 
  const ragFile = ref(null);
  const qtype = ref('');
  const level = ref('');
  const isGenerating = ref(false);
  const currentQuestion = ref(null);
  const questionContent = ref('');
  const questionHint = ref('');
  const questionTargetFile = ref('');

  // 
  const studentAnswer = ref('');
  const isGrading = ref(false);
  const gradingResult = ref(null);

  /**
   *  (Handle File Selection)
   */
  const handleFileSelect = (event) => {
    const file = event.target.files[0];
    if (file) {
      ragFile.value = file;
    }
  };

  /**
   *  (Generate Question)
   */
  const generateQuestion = async () => {
    isGenerating.value = true;
    currentQuestion.value = null;
    questionContent.value = '';
    questionHint.value = '';
    questionTargetFile.value = '';
    gradingResult.value = null;
    studentAnswer.value = '';

    try {
      const formData = new FormData();
      //  FormData
      //  rag_db.zip
      if (ragFile.value) {
        formData.append('file', ragFile.value);
      }
      formData.append('qtype', qtype.value);
      formData.append('level', level.value);

      const response = await fetch(`${API_BASE.value}/api/generate_question`, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(errorText || `HTTP ${response.status}: ${response.statusText}`);
      }

      const data = await response.json();
      currentQuestion.value = data;
      questionContent.value = data.question_content || '';
      questionHint.value = data.hint || '';
      questionTargetFile.value = data.target_filename || '';
    } catch (error) {
      console.error(':', error);

      // 
      let errorMessage = error.message;

      if (error.message === 'Failed to fetch' || error.name === 'TypeError') {
        errorMessage =
          ` API  (${API_BASE.value})\n\n` +
          `\n` +
          `1. API  ${API_BASE.value}\n` +
          `2. CORS  ${window.location.origin} \n` +
          `3. \n\n` +
          ` API  API_BASE `;
      } else if (error.message.includes('NetworkError') || error.message.includes('network')) {
        errorMessage = `\n\n\n1. API \n2. `;
      }

      alert(`${errorMessage}`);
    } finally {
      isGenerating.value = false;
    }
  };

  /**
   *  (Grade Submission)
   */
  const gradeSubmission = async () => {
    if (!studentAnswer.value.trim()) {
      alert('');
      return;
    }

    if (!questionContent.value) {
      alert('');
      return;
    }

    isGrading.value = true;
    gradingResult.value = null;

    try {
      const formData = new FormData();
      //  FormData
      //  rag_db.zip
      if (ragFile.value) {
        formData.append('file', ragFile.value);
      }
      formData.append('question_text', questionContent.value);
      formData.append('student_answer', studentAnswer.value);
      formData.append('qtype', qtype.value);

      const response = await fetch(`${API_BASE.value}/api/grade_submission`, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(errorText || `HTTP ${response.status}: ${response.statusText}`);
      }

      const data = await response.json();
      gradingResult.value = data;
    } catch (error) {
      console.error(':', error);

      // 
      let errorMessage = error.message;

      if (error.message === 'Failed to fetch' || error.name === 'TypeError') {
        errorMessage =
          ` API  (${API_BASE.value})\n\n` +
          `\n` +
          `1. API  ${API_BASE.value}\n` +
          `2. CORS  ${window.location.origin} \n` +
          `3. \n\n` +
          ` API  API_BASE `;
      } else if (error.message.includes('NetworkError') || error.message.includes('network')) {
        errorMessage = `\n\n\n1. API \n2. `;
      }

      alert(`${errorMessage}`);
    } finally {
      isGrading.value = false;
    }
  };
</script>

<template>
  <!--   -->
  <div class="d-flex flex-column my-bgcolor-gray-200 h-100">
    <!--   -->
    <div v-if="visibleLayers.length > 0" class="">
      <ul class="nav nav-tabs nav-fill">
        <li
          v-for="layer in visibleLayers"
          :key="layer.layerId"
          class="nav-item d-flex flex-column align-items-center"
        >
          <!-- tab -->
          <div
            class="btn nav-link rounded-0 border-0 position-relative d-flex align-items-center justify-content-center my-bgcolor-gray-200"
            :class="{
              active: activeLayerTab === layer.layerId,
            }"
            @click="setActiveLayerTab(layer.layerId)"
          >
            <span>
              <span v-if="getLayerFullTitle(layer).groupName" class="my-title-xs-gray"
                >{{ getLayerFullTitle(layer).groupName }} -
              </span>
              <span class="my-title-sm-black">{{ getLayerFullTitle(layer).layerName }}</span>
            </span>
          </div>
          <div class="w-100" :class="`my-bgcolor-${layer.colorName}`" style="min-height: 4px"></div>
        </li>
      </ul>
    </div>

    <!--  -->
    <div v-if="visibleLayers.length > 0" class="flex-grow-1 overflow-auto my-bgcolor-white p-3">
      <!--  test_layer AI  -->
      <div v-if="isTestLayer" class="ai-assistant-container">
        <h5 class="my-title-md-black mb-4"> AI  (RAG )</h5>

        <!-- 1.  RAG  -->
        <div class="ai-section mb-4 p-3 border rounded">
          <h6 class="my-title-sm-black mb-3">1.  RAG </h6>

          <div class="mb-3">
            <label class="form-label my-title-xs-gray"
              > rag.zip  .zip</label
            >
            <input type="file" class="form-control" accept=".zip" @change="handleFileSelect" />
            <small v-if="ragFile" class="text-muted">: {{ ragFile.name }}</small>
            <small v-else class="text-muted"> rag_db.zip</small>
          </div>

          <div class="row g-3 mb-3">
            <div class="col-md-6">
              <label class="form-label my-title-xs-gray"> (Qtype)</label>
              <select v-model="qtype" class="form-select">
                <option value=""> (R Code)</option>
                <option value=""></option>
              </select>
            </div>
            <div class="col-md-6">
              <label class="form-label my-title-xs-gray"> (Level)</label>
              <select v-model="level" class="form-select">
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>
          </div>

          <button
            class="btn btn-primary w-100"
            @click="generateQuestion"
            :disabled="isGenerating"
          >
            <span
              v-if="isGenerating"
              class="spinner-border spinner-border-sm me-2"
              role="status"
            ></span>
            {{ isGenerating ? ' AI  Zip ... ( 10-20 )' : ' ' }}
          </button>
        </div>

        <!-- 2.  -->
        <div v-if="currentQuestion" class="ai-section mb-4 p-3 border rounded">
          <h6 class="my-title-sm-black mb-3">2. </h6>

          <div class="result-box p-3 mb-3 bg-light rounded">
            <h6 class="my-title-xs-black mb-2"> </h6>
            <div class="question-content mb-3">{{ questionContent }}</div>
            <hr />
            <p class="mb-1">
              <strong> </strong>
              <span>{{ questionHint }}</span>
            </p>
            <p class="mb-0">
              <strong> </strong>
              <code>{{ questionTargetFile }}</code>
            </p>
          </div>

          <div class="mb-3">
            <label class="form-label my-title-xs-gray"> ()</label>
            <textarea
              v-model="studentAnswer"
              class="form-control"
              rows="8"
              placeholder="library(sf); st_read(...)"
            ></textarea>
          </div>

          <button
            class="btn btn-success w-100"
            @click="gradeSubmission"
            :disabled="!studentAnswer.trim() || isGrading"
          >
            <span
              v-if="isGrading"
              class="spinner-border spinner-border-sm me-2"
              role="status"
            ></span>
            {{ isGrading ? ' AI ...' : ' ' }}
          </button>
        </div>

        <!-- 3.  -->
        <div v-if="gradingResult" class="ai-section mb-4 p-3 border rounded">
          <h6 class="my-title-sm-black mb-3">3. </h6>

          <div class="result-box p-3 bg-light rounded">
            <div class="d-flex justify-content-between align-items-center mb-3">
              <div>
                <span class="score-badge">{{ gradingResult.score }}</span> / 10
              </div>
              <div class="level-badge">{{ gradingResult.level }}</div>
            </div>
            <hr />

            <div v-if="gradingResult.strengths && gradingResult.strengths.length > 0" class="mb-3">
              <h6 class="my-title-xs-black mb-2"> </h6>
              <ul class="mb-0">
                <li
                  v-for="(strength, index) in gradingResult.strengths"
                  :key="index"
                  class="text-success"
                >
                  {{ strength }}
                </li>
              </ul>
            </div>

            <div
              v-if="gradingResult.weaknesses && gradingResult.weaknesses.length > 0"
              class="mb-3"
            >
              <h6 class="my-title-xs-black mb-2"> </h6>
              <ul class="mb-0">
                <li
                  v-for="(weakness, index) in gradingResult.weaknesses"
                  :key="index"
                  class="text-warning"
                >
                  {{ weakness }}
                </li>
              </ul>
            </div>

            <div
              v-if="gradingResult.action_items && gradingResult.action_items.length > 0"
              class="mb-3"
            >
              <h6 class="my-title-xs-black mb-2"> </h6>
              <ul class="mb-0">
                <li v-for="(item, index) in gradingResult.action_items" :key="index">
                  {{ item }}
                </li>
              </ul>
            </div>

            <div v-if="gradingResult.rubric && gradingResult.rubric.length > 0" class="mb-0">
              <h6 class="my-title-xs-black mb-2"> </h6>
              <ul class="mb-0">
                <li v-for="(item, index) in gradingResult.rubric" :key="index">
                  {{ item }}
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <!--   -->
      <div v-else>
        <!--   -->
        <div class="mb-4">
          <h5 class="my-title-md-black">{{ currentLayerName }}</h5>
        </div>

        <!--   -->
        <div v-if="currentLayerWorkData && workDataEntries.length > 0">
          <div v-for="[key, value] in workDataEntries" :key="key" class="mb-3">
            <div class="my-title-xs-gray pb-1">{{ key }}</div>
            <div class="my-content-sm-black pb-1">
              {{ formatDisplayValue(value) }}
            </div>
          </div>
        </div>
        <div v-else-if="!currentLayerWorkData" class="text-center py-5">
          <div class="my-title-md-gray"></div>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-else class="flex-grow-1 d-flex align-items-center justify-content-center">
      <div class="text-center">
        <div class="my-title-md-gray p-3"></div>
      </div>
    </div>
  </div>
</template>

<style scoped>
  /* AI  */
  .ai-assistant-container {
    max-width: 100%;
  }

  .ai-section {
    background: #fff;
    border-left: 4px solid #3498db;
  }

  .result-box {
    background: #f8f9fa;
    border: 1px solid #e9ecef;
  }

  .question-content {
    white-space: pre-wrap;
    word-wrap: break-word;
    line-height: 1.6;
  }

  .score-badge {
    font-size: 2em;
    font-weight: bold;
    color: #3498db;
  }

  .level-badge {
    font-size: 1.2em;
    color: #7f8c8d;
  }

  .form-label {
    font-weight: 500;
    margin-bottom: 0.5rem;
  }

  .btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }

  .spinner-border-sm {
    width: 1rem;
    height: 1rem;
  }
</style>
